  src: url(./fonts/TTLakes-Regular.eot);
  src: local("TT Lakes"), local("TTLakes-Regular"), url(./fonts/TTLakes-Regular.eot?#iefix) format("embedded-opentype"), url(./fonts/TTLakes-Regular.woff) format("font/woff"), url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: 400; }

@font-face {
  font-family: 'TT Lakes';
  src: url(./fonts/TTLakes-Medium.eot);
  src: local("TT Lakes Medium"), local("TTLakes-Medium"), url(./fonts/TTLakes-Medium.eot?#iefix) format("embedded-opentype"), url(./fonts/TTLakes-Medium.woff) format("font/woff"), url(./fonts/TTLakes-Medium.ttf) format("truetype");
  font-weight: 500; }

@font-face {
  font-family: 'TT Lakes';
  src: url(./fonts/TTLakes-Bold.eot);
  src: local("TT Lakes Bold"), local("TTLakes-Bold"), url(./fonts/TTLakes-Bold.eot?#iefix) format("embedded-opentype"), url(./fonts/TTLakes-Bold.woff) format("font/woff"), url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: 900; }

.header {
  display: flex;
  justify-content: space-between;
  padding: calc(16px + 8 * (100% - 320px) / 400); }

.header__menu {
  display: flex;
  position: relative;
  min-width: 155px;
  justify-content: space-between; }

.buttons {
  display: flex;
  align-items: center; }

@media (min-width: 1119px) {
  .header__menu {
    display: none; }
  .header {
    position: absolute;
    top: -6px;
    right: 0; } }

@media (min-width: 767px) and (max-width: 1119px) {
  .header__menu {
    min-width: 180px; }
  .header__burger::after {
    content: "";
    width: 4px;
    height: 32px;
    border-radius: 2px;
    background: #EAEAEA;
    position: absolute;
    left: 60px;
    bottom: 10px; } }

.large-menu {
  display: none; }

@media (min-width: 768px) and (max-width: 1119px) {
  .large-menu {
    display: flex;
    gap: 16px;
    height: 40px;
    min-width: 170px; } }

.h-buttons {
  display: flex;
  min-width: 90px;
  height: 40px;
  gap: 16px;
  position: relative; }

.h-buttons::before {
  content: "";
  width: 4px;
  height: 32px;
  border-radius: 2px;
  background: #EAEAEA;
  position: relative;
  top: 4px; }

.h-buttons__label {
  display: none; }

@media (min-width: 1119px) {
  .h-buttons::before {
    display: none; }
  .h-buttons__button {
    display: flex;
    align-items: center;
    gap: 8px; }
  .h-buttons__label {
    display: inline;
    font-size: 16px;
    line-height: 24px; } }

.page-header {
  padding: 24px 0; }

.page-header__title {
  height: 40px;
  margin: 0;
  font-size: 28px;
  line-height: 143%;
  letter-spacing: -0.6px;
  color: #1B1C21;
  font-weight: 900; }

.page-nav {
  margin: 5px;
  display: flex;
  overflow: auto;
  padding-bottom: 0; }

@media (min-width: 1119px) {
  .page-nav {
    flex-wrap: wrap; }
  .page-nav__link {
    padding: 8px;
    height: 24px; } }

.page-nav__link {
  padding: 8px;
  height: 24px;
  margin-right: 16px;
  white-space: nowrap;
  color: #7E7E82;
  font-size: 16px;
  line-height: 24px; }

.page-nav__link-state--active {
  border: 2px solid #B8FFEC;
  border-radius: 6px;
  order: -1;
  background-color: #FFFFFF;
  padding: 6px; }

.menu {
  position: fixed;
  display: flex;
  flex-direction: column;
  color: #1B1C21;
  font-family: "TT Lakes";
  letter-spacing: -0.2px;
  font-size: 16px;
  line-height: 24px;
  width: 100%;
  height: 100%;
  max-height: 1024px;
  padding: 24px;
  box-shadow: 16px 0px 52px 0px #0e185033, -2px 0px 4px 0px #454f7e05;
  box-sizing: border-box;
  z-index: 2; }

@media (min-width: 360px) {
  .menu {
    width: 320px; } }

@media (min-width: 521px) {
  .menu {
    width: 440px; } }

@media (min-width: 1440px) {
  .m-header__close-b {
    display: none; }
  .menu {
    box-shadow: none;
    width: 320px; } }

.m-header {
  display: flex;
  padding-bottom: 50px;
  width: 100%;
  justify-content: space-between;
  align-items: center; }

.m-header__left {
  display: flex;
  align-items: center;
  gap: 16px; }

.m-main {
  flex: 1 0 50%; }

.m-main__list {
  display: flex;
  flex-direction: column;
  gap: 24px;
  list-style-type: none; }

.m-main__item {
  font-family: "TT Lakes";
  font-size: 16px;
  letter-spacing: -0.2px;
  font-weight: bold; }

.m-main__item--active {
  position: relative;
  color: #7E7E82; }

.m-main__item--active::before {
  content: '';
  height: 32px;
  width: 4px;
  border-radius: 2px;
  background: #41F6D7;
  position: absolute;
  left: -25px;
  top: -5px; }

.m-footer {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  gap: 40px;
  min-height: 200px; }

.m-footer__contacts {
  display: flex;
  flex-direction: column;
  gap: 16px; }

.f-buttons {
  display: flex;
  gap: 16px; }

.contacts__email {
  font-size: 16px;
  line-height: 24px; }

.contacts__phone {
  font-size: 24px;
  font-weight: bold;
  line-height: 32px;
  letter-spacing: -0.6px;
  color: #1B1C21; }

.lang {
  display: flex;
  gap: 8px; }

.lang__input {
  position: absolute;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none; }

.lang__item:has(.lang__input:checked) {
  color: #7E7E82; }

.popup {
  position: fixed;
  box-sizing: border-box;
  top: 0;
  right: 0;
  width: 100%;
  min-height: 100%;
  padding: 24px 16px;
  background: #FFFFFF;
  box-shadow: 16px 0px 52px 0px #0E185033, -2px 0px 4px 0px #454F7E05;
  z-index: 2; }

.popup__form {
  display: flex;
  flex-direction: column;
  padding-top: 40px; }

@media (min-width: 521px) {
  .popup {
    width: 440px;
    padding: 0 16px 24px 16px; }
  .popup__close-b {
    position: absolute;
    left: -70px;
    top: 40px; } }

.popup__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.6px;
  font-weight: bold;
  padding-bottom: 24px;
  position: relative; }

.popup__title::before {
  content: "";
  height: 40px;
  width: 4px;
  border-radius: 2px;
  background: #41F6D7;
  position: absolute;
  left: -18px;
  bottom: 9px; }

.popup__input-group {
  display: flex;
  flex-direction: column;
  gap: 16px; }

.popup__input {
  width: 100%;
  height: 48px;
  padding: 12px 16px;
  box-sizing: border-box;
  border: 1px solid #EAEAEA;
  border-radius: 8px;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC; }

.popup__message {
  height: 118px; }

.popup__comment {
  padding-top: 16px;
  padding-bottom: 18px; }

.popup__comment a {
  text-decoration: none;
  color: #FF3E79; }

.popup__button {
  align-self: flex-end; }

.post {
  padding-bottom: 16px;
  display: flex;
  justify-content: space-between; }

.b-main__button {
  padding-left: 8px; }

.post__b-main {
  max-width: 50%;
  margin-right: 48px; }

.post__aside {
  align-self: center; }

@media (max-width: 767px) {
  .post {
    flex-direction: column; } }

.text {
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  text-align: start; }

.text__entry {
  margin-top: 0; }

@media (max-width: 767px) {
  .text__middle {
    display: none; }
  .post__b-main {
    max-width: 100%;
    margin-right: 0;
    margin-bottom: 10px; } }

@media (max-width: 1119px) {
  .text__all {
    display: none; } }

.post__image {
  width: 100%; }

@media (min-width: 768px) {
  .post__image {
    border-radius: 3%; }
  .post__aside {
    min-width: calc(50% - calc(16px + 8 * (100% - 320px) / 400)); } }

.reverse-button::before {
  content: url(./static/button.svg);
  display: inline-block;
  margin-right: 8px;
  transform: rotate(180deg); }

.title {
  font-weight: normal;
  color: #7E7E82;
  font-size: 16px;
  letter-spacing: -0.2px;
  text-transform: uppercase;
  background: linear-gradient(90deg, #F8F8F8 1.39%, #FFFFFF 100%);
  padding: 16px 24px;
  margin: 0;
  position: relative;
  left: -24px;
  width: 100%; }

.galery {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 16px;
  padding: 24px 0; }

.main-section {
  padding-bottom: 32px; }

.item {
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA;
  border-radius: 6px;
  box-sizing: border-box;
  width: 224px; }

@media (min-width: 1119px) {
  .item {
    width: 240px; } }

.services__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 224px;
  height: 72px;
  padding-left: 16px;
  padding-right: 16px; }

.services__logo {
  max-width: 128px; }

.repair__item {
  display: flex;
  position: relative;
  justify-content: flex-end;
  align-items: flex-end;
  height: 160px;
  padding: 16px; }

.repair__label {
  position: absolute;
  left: 16px;
  top: 16px;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px; }

.prices {
  padding-bottom: 47px; }

.prices__table {
  font-size: 14px;
  font-weight: normal;
  line-height: 18px;
  letter-spacing: 0.2px;
  border-collapse: collapse;
  width: 100%; }

.prices__table tr {
  width: 100%;
  background: #FFFFFF; }

.prices__table tr:first-child {
  color: #7E7E82;
  mix-blend-mode: normal;
  opacity: 0.72;
  background: transparent; }

.prices__table th,
.prices__table td {
  padding: 16px;
  text-align: start; }

.prices__table td {
  border-bottom: 1px solid #EAEAEA; }

.prices__table tr:nth-child(2) td:first-child {
  border-top-left-radius: 6px; }

.prices__table tr:nth-child(2) td:last-child {
  border-top-right-radius: 6px; }

.prices__table tr:nth-child(6) td:first-child {
  border-bottom-left-radius: 6px; }

.prices__table tr:nth-child(6) td:last-child {
  border-bottom-right-radius: 6px; }

.prices__button {
  padding: 0 12px; }

.prices__item {
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: flex-start;
  height: 200px;
  width: 260px;
  padding: 16px;
  gap: 16px; }

.swiper-prices__label {
  font-size: 14px;
  line-height: 18px;
  color: #1B1C21;
  letter-spacing: 0.2px; }

.swiper-prices__label--property {
  color: #7E7E82;
  mix-blend-mode: normal;
  opacity: 0.72; }

.swiper-prices__group {
  gap: 4px; }

.swiper-prices__button {
  position: absolute;
  bottom: 20px;
  right: 20px; }

.prices__warning {
  width: 50%;
  padding-top: 24px;
  padding-bottom: 16px; }

@media (max-width: 320px) {
  .prices__warning {
    width: 100%; } }

.prices__offer-button {
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-align: left; }

.prices__offer-button::after {
  content: ">";
  color: #FF3E79; }

.swiper {
  --swiper-pagination-bullet-inactive-color: #DDDDDD;
  --swiper-pagination-color: #B5B6BC;
  --swiper-pagination-bullet-size: 12px; }

.swiper-pagination {
  position: static !important; }

main {
  background: #F8F8F8;
  font-weight: normal;
  color: #1B1C21;
  padding: 0 calc(16px + 8 * (100% - 320px) / 400); }

button,
th,
tr,
ul,
li,
input {
  font-family: "TT Lakes";
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  font-weight: normal;
  color: #1B1C21; }

.button {
  height: 24px;
  font-size: 16px;
  letter-spacing: -0.2px;
  font-style: normal;
  font-weight: 500; }

.button::before {
  content: url(./static/button.svg);
  margin-right: 8px; }

.title-divider {
  position: relative; }

.title-divider::before {
  content: "";
  height: 40px;
  width: 4px;
  border-radius: 2px;
  background: #41F6D7;
  position: absolute;
  left: calc(calc(16px + 8 * (100% - 320px) / 400) * (-1)); }

.white-space {
  position: fixed;
  top: 0;
  left: 0;
  background: #FFFFFF;
  mix-blend-mode: normal;
  opacity: 0.96;
  height: 100%;
  width: 100%;
  z-index: 2; }

.popup-button {
  text-transform: uppercase;
  color: #FFFFFF;
  letter-spacing: -0.15px;
  font-size: 12px;
  line-height: 24px;
  background: #FF3E79;
  border-radius: 16px;
  font-weight: bold;
  padding: 0 8px; }

.popup-button::after {
  content: ">";
  margin-left: 12px;
  font-size: 18px;
  line-height: 32px; }

@media (min-width: 1440px) {
  .body__wrapper {
    width: calc(100% - 320px);
    position: absolute;
    right: 0; } }

.footer {
  color: #7E7E82;
  background: #F8F8F8;
  padding: calc(16px + 8 * (100% - 320px) / 400);
  font-size: 14px;
  line-height: 16px;
  mix-blend-mode: normal;
  opacity: 0.72;
  display: flex;
  justify-content: space-between; }

.footer div {
  width: 33%; }

.footer__date {
  text-align: left; }

.footer__policy {
  text-align: center; }

.footer__warning {
  text-align: right; }

@media (max-width: 320px) {
  .footer {
    flex-direction: column;
    gap: 16px; }
  .footer div {
    width: 100%;
    text-align: center; } }

.hidden-element {
  display: none !important; }

.invisible-element {
  visibility: hidden; }

body {
  margin: 0;
  padding: 0;
  font-family: "TT Lakes"; }
